@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

$roboto-slab : Helvetica Neue, Helvetica, Arial, sans-serif;
$lobster : 'Lobster', cursive;

$bgColor : #216353;
$navColor : rgb(27, 27, 27);
$contentColor : #ff1e566e; 
$cardColor : #e7ebeb;
$cardColorHover : #ccd3d3;

@mixin bacgroundImage($path, $position:0 0, $repeat:no-repeat, $size:cover){
    background: {
        image : url($path);
        position: $position;
        repeat : $repeat;
        size : $size
    };
}

.loading {
    height: 100vh;
    width: 100vw;
    // background-color: white;
    opacity: 100%;
}

body {
    margin : 0;
    padding: 0;
    overflow: hidden;
    font-family: $roboto-slab;
}

lottie-player {

    position: fixed;
    z-index: 9999999;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%)

}


.msg {
    margin: 5px;
    width:95%;
    border: 1px solid;
    padding:10px;
    opacity: 80%;
    color: grey;
  }

  
  .msg-error {
    border-color: #d32f2f;
    background-color: #ef5350;
    color: white;
  }

.login-container, .register-container {
    //  top: 50%;
    // left: 50%;
    // margin-right:-50%;
    // transform: translate(-50%, -50%);
    margin-top: 5%;
    margin-bottom: 5%;
    border-radius: 4px;
}
.login-form, .register-form {
  
    margin: auto;
    top: 50px;
    display: flex;
    flex-direction:row;
    justify-content: flex-start;
    width: 600px;
    height: 450px;
    border-radius: 2px;
  
    box-shadow: 5px 0px 10px 0px rgb(177, 177, 177);
}



h2 {
    color: $bgColor;
    font-size: 24px;
}

.left {
   margin: 10px;
    
}

.right {
    background-color:$bgColor;
    width: 200px;
    flex: 1;
    
}

.right h1 {
    color: white;
    font-family: $lobster;
    text-align: center;
    font-size: 64px;
    margin-top: 40%;
    opacity: 90%;
}

.left form input[type=text]:focus {
    border-bottom: 1px solid $bgColor;
    box-shadow: 0 1px 0 0 #000;
  }

p {
    font-size: 12px;
   color: grey;
}


//dashboard
.container {
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100vw;
    display:flex;
    flex-direction:column;
    background-color:$bgColor;
    // @include bacgroundImage('/img/bg.jpg');
}

.navbar {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    background-color: $navColor;
    height:60px;
    opacity: 32%;
    justify-content: space-between;

} 

.kanban-title h4 {
    color: white;
    flex: 1;
    margin: 5px;
    font-family: $lobster;
}

.navbar #logout-btn {
    opacity: 100%;
    color: white;
    border: none;
    margin: 5px;
    background-color:rgb(201, 201, 201);
    border-radius: 2px;
    width: 64px; 
   
}

.navbar #logout-btn:hover {
    cursor: pointer; 
    // opacity: 50%;
}


.content {
    height: 100vh;
    display:flex;
    justify-content: flex-start;
    align-items: flex-start;
    overflow-x: auto;
   
}

.card-list, .card-list-add {
    display: flex;
    flex-direction: column; 
    background-color: $cardColor;
    margin: 10px;
    padding: 2px;
    width: 240px;
    border-radius: 3px;
    min-width: 250px;
}

.card-list-add {
    width: 200px;
}

.card-list-add div input {
    margin: auto;
}

.card-title h3 {
    font-size: 14px;
    padding: 5px;
    margin-top: 10px;
 
}

.card-title {
    height: 40px;
}

.card-content-box {
    display: flex;
    flex-direction: column;
    min-height: auto;
    max-height: 200px;
}

.card-content {
    background-color: white;
    margin: 4px;
    border-radius: 2px;
    padding: 5px;
    box-shadow: 1px 0px 2px 0px grey;
    font-size: 14px;
    display: flex;
    flex-direction: column;   
    justify-content: right;
    height:auto;
    width: auto; 
 
}

.card-content button {
    border: none;
    height: 30px;
    margin-left: 150px;
}

.card-content p {
    margin: 0;
    color: black;
}

.card-content button:hover {
    cursor: pointer;
}

.card-content .options {
    text-align: right;
    margin: 0;
}

.card-content .options i:hover {
    cursor: pointer;
    display: block;
}



.card-btn button {
    height:32px;
    width: 100%;
    text-align: left;   
    background-color: $cardColor; 
    border: 0;
    padding: 5px;
    border-radius: 3px;
   
}

.card-btn button:hover {
   background-color: $cardColorHover;
   cursor: pointer;
  
}

.card-btn {
    display: flex;
    flex-direction: row;
    padding: 5px;
}

.card-btn #edit {
    width: auto;
}

.card-btn #delete {
    flex: 1;
}

//dropdown
.card-content:hover {
    background-color: rgb(245, 245, 245);
    cursor: pointer;
}


// .card-content:hover ul li ul.dropdown {
//     display: none;
// }

ul{
    margin: 0;
    padding: 0;
    list-style: none;
    background:none;
    text-align: right;
    height: auto;
    
}
ul li{
    display: inline-block;
    position: relative;
    line-height: 32px;
}
ul li a{
    display: block;
    padding: 0px 10px;
    color: gray;
    text-decoration: none;
    text-align: left;


}
ul li a:hover{
    color: #fff;
    background: $bgColor;
}

ul li ul.dropdown{
     /* Set width of the dropdown */
    width: 200;
    background: #f2f2f2;
    position: absolute;
    z-index: 999;
    left: 0;
    display: none;
  
    
}


ul li:hover ul.dropdown{
    display: block;	/* Display the dropdown */
}
ul li ul.dropdown li{
    display: block;
    width: 160px;
}


.move {
    display: flex;
    flex-direction: row;

}

.move select {
    flex: 1;
    width: auto;
}

.move button {
    margin: 2px;
    height: auto;
}

